import React, { useEffect } from "react";
import { useInView } from "react-intersection-observer"
import { fadeIn, fadeOutRightBig,fadeInLeft } from 'react-animations';
import 'html5-device-mockups/dist/device-mockups.min.css'
import { useMediaQuery } from 'react-responsive'
import Radium, { StyleRoot } from 'radium';
import { IMac } from 'react-device-mockups';
import {
  Container,
  Row,
  Col,
} from "reactstrap";

function FirstSlide() {
  const [ref, inView] = useInView({ threshold: 0 })
  const isTabletOrMobile = useMediaQuery({ query: '(max-width: 1224px)' })
  const animationStyle = {
    fadeInLeftBig: {
      animation: 'x 1.5s',
      animationName: Radium.keyframes(fadeInLeft, 'fadeInLeft'),
    //  / color:'black'
    },
    fadeOutUpBig: {
      animation: 'x 10.5s',
      animationName: Radium.keyframes(fadeOutRightBig, 'fadeOutRightBig'),
      opacity: 10
    }
  }
  return (
    <>
      <div
      
        className="page-header"
        style={{
          backgroundColor: 'white',
          height: '500px',

          marginTop:'100px',
          // backgroundImage:
          //  ' url("../../assets/img/antoine-barres.jpg ")',
         // backgroundColor: '#4084f3'
        }}
      >
        <Container>
          <Row >
            <Col md="6">
              <div  ref={ref} >
                <div className="description">
                  <StyleRoot>
                    {inView?<h1 className="description" style={inView ? animationStyle.fadeInLeftBig : null} >
                      Agricom is the next generation food supply business, on a mission to transform how food is accessed and distributed within cities.
                    </h1>:null}
                  </StyleRoot>
                </div>
              </div>
            </Col>
            <Col md="6" className="d-flex justify-content-start" >
              {!isTabletOrMobile ? <div >
                <IMac width={750} orientation='landscape' color='black' >
                  <iframe title='showcase' src='https://example.com' style={{
                    width: '100%',
                    height: '100%',
                    margin: 0
                  }} />
                </IMac>
              </div> : null}
            </Col>
          </Row>
        </Container>

      </div>
    </>
  );
}

export default FirstSlide;
