import React from "react";
import { Link } from "react-router-dom";
// nodejs library that concatenates strings
import classnames from "classnames";
import styles from './styles.scss'
import  './styles.scss'
// reactstrap components
import {
  Collapse,
  NavbarBrand,
  Navbar,
  NavItem,
  NavLink,
  Nav,
  Container,
  Button,
} from "reactstrap";
import logo from '../../assets/img/agricom.png'
import { useMediaQuery } from 'react-responsive'
function ExamplesNavbar(props) {
  const {OurPromiseRef,SustainabilityRef,contactUsRef} =props
  const executeScrollOurPromise = () => OurPromiseRef.current.scrollIntoView({ behavior: "smooth" });  
  const executeScrollOurSustainability = () => SustainabilityRef.current.scrollIntoView({ behavior: "smooth" });  
  const executeScrollcontactUs = () => contactUsRef.current.scrollIntoView({ behavior: "smooth" });  
  const [navbarColor, setNavbarColor] = React.useState("navbar-transparent");
  const [navbarCollapse, setNavbarCollapse] = React.useState(false);
  const isTabletOrMobile = useMediaQuery({ query: '(max-width: 1224px)' })
  const toggleNavbarCollapse = () => {
    setNavbarCollapse(!navbarCollapse);
    document.documentElement.classList.toggle("nav-open");
  };

  React.useEffect(() => {
    const updateNavbarColor = () => {
      if (
        document.documentElement.scrollTop > 299 ||
        document.body.scrollTop > 299
      ) {
        setNavbarColor("");
      } else if (
        document.documentElement.scrollTop < 300 ||
        document.body.scrollTop < 300
      ) {
        setNavbarColor("navbar-transparent");
      }
    };

    window.addEventListener("scroll", updateNavbarColor);

    return function cleanup() {
      window.removeEventListener("scroll", updateNavbarColor);
    };
  });
  return (
    <Navbar
      className={classnames("fixed-top", navbarColor)}
      color-on-scroll="300"
      expand="lg"
      
    >
      <Container>

        <div className="navbar-translate"  >
          <NavbarBrand
            data-placement="bottom"
            to="/index"
            target="_blank"
            title=""
         
          >
            <div    style={!isTabletOrMobile?{position:'absolute',paddingTop:'15px'}:null}>
            <img className={[navbarColor==""?'img':'logo']} src={logo} style={{color:'red',fill:'red'}}/>
            </div>
          
          </NavbarBrand>
          <button
            aria-expanded={navbarCollapse}
            className={classnames("navbar-toggler navbar-toggler", {
              toggled: navbarCollapse,
            })}
            onClick={toggleNavbarCollapse}
          >
            <span className="navbar-toggler-bar bar1" />
            <span className="navbar-toggler-bar bar2" />
            <span className="navbar-toggler-bar bar3" />
          </button>
        </div>
        <Collapse
          className="justify-content-end"
          navbar
          isOpen={navbarCollapse}
        >
          <Nav navbar >
            <NavItem>
              <NavLink  href="#" onClick={()=>{executeScrollOurPromise()}} >
                Our Promise
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="#"  onClick={()=>{executeScrollOurSustainability()}}>
               Sustaibility
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="#" onClick={()=>{executeScrollcontactUs()}}  >
               Get Sarted
              </NavLink>
            </NavItem>
            <NavItem>
            <NavLink href="#" onClick={()=>{executeScrollcontactUs()}}  >
                {/* <i className="nc-icon nc-layout-11" /> */}
                 Contact Us
              </NavLink>
            </NavItem>
          
      
           
            <NavItem>
              <Button
                className="btn-round"
                color="danger"
                href="https://main.agricom.app"
                target="_blank"
              >
            Sign In
              </Button>
            </NavItem>
          </Nav>
        </Collapse>
      </Container>
    </Navbar>
  );
}

export default ExamplesNavbar;
